import axios from "axios";

export async function ExtrairTkenEretornarID () {
    // Extrair o token do localStorage
    const token = localStorage.getItem('token');

    // Verificar se o token foi encontrado
    if (token) {
        // Decodificar o token para obter os dados do usurio
        const tokenData = JSON.parse(atob(token.split('.')[1]));

        // Extrair o nome de usurio do tokenData
        const username = tokenData.username;

        // Agora voc pode usar o nome de usurio conforme necessrio
        console.log('Nome de usurio:', username);

        try {
            // Fazer a solicitao HTTP para obter o ID do usurio
            const response = await axios.get(`http://localhost:3001/login/getUserID/${username}`);
            
            // A resposta da solicitao HTTP estar disponvel aqui
            return response.data.userID
        } catch (error) {
            // Se ocorrer um erro durante a solicitao HTTP
            console.error('Erro ao fazer a solicitao HTTP:', error);
        }
    } else {
        console.error('Token no encontrado no localStorage');
    }
}
