import axios from "axios";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Section } from "./background";
import { CircularProgress, Grid } from "@mui/material";

export function PrivateRoute( { Carregando, loading } ) {
  const navigate = useNavigate();
  const [isValidToken, setIsValidToken] = useState(false);


  useEffect(() => {
    // Verifica se h um token no localStorage
    const token = localStorage.getItem("token");
    console.log("Token:", token);
    if (token) {
      // Faz uma requisio para verificar se o token  vlido
      axios
        .post(
          "http://localhost:3001/login/verificar-token",
          { token },
          {
            headers: {
              "Content-Type": "application/json",
            },
          }
        )
        .then((response) => {
          console.log("Resposta da verificao de token:", response.data);
          const { success } = response.data;
          if (success) {
            setTimeout(() => {

                setIsValidToken(true)
                Carregando(loading)
            }, 2000)

          } else {
            // Se o token no for vlido, redireciona para a pgina inicial
            setTimeout(() => {
              navigate("/");
        }, 1000);
          }
        })
        .catch((error) => {
          console.error("Erro ao verificar o token:", error);
          // Em caso de erro na requisio, redireciona para a pgina inicial
          setTimeout(() => {
            navigate("/");
      }, 1000);
        });
    } else {
      // Se no houver token no localStorage, redireciona para a pgina inicial
      setTimeout(() => {
          navigate("/");
    }, 1000);
    }
  }, []); 

  return (
    <Section>
        <Grid container sx={{ minHeight: "100vh", display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', color: '#BFCEDE', paddingBottom: '150px', paddingTop: '10%' }}>
            <CircularProgress color="secondary" size={85} />
        </Grid>
    </Section>
  ); 
}
